<template>
<!--
  /
  listTypetext
  headers: headers
  listType: text/picture/picture-card
  action:
  token:token
  fileList:
  limit:
  data: POST
  style: 
-->

<el-upload
  v-if="item.httpRequest"
  v-bind="$attrs"
  :before-upload="(file) => onBeforeUpload(file, item)"
  :on-remove="handleFileRemove"
  :on-exceed="onFileExceed"
  :http-request="(info)=>item.httpRequest(info, fileList)"
>
  <template>
    <!-- -->
    <template v-if="!item.listType || item.listType === 'picture-card'">
      <i class="el-icon-plus" ></i>
    </template>
    <template v-else>
      <el-button size="small" type="primary"></el-button>
    </template>
  </template>
</el-upload>
<el-upload
  v-else
  v-bind="$attrs"
  :file-list="fileList"
  :before-upload="(file) => onBeforeUpload(file, item)"
  :on-remove="handleFileRemove"
  :on-success="
    (response, file,fileList) => onUploadSuccess(response, file, fileList)
  "
  :on-exceed="onFileExceed"
>
  <template>
    <!--  -->
    <input type="hidden" :value="value" />
    <!-- -->
    <template v-if="!item.listType || item.listType === 'picture-card'">
      <i class="el-icon-plus" ></i>
    </template>
    <template v-else>
      <el-button size="small" type="primary"></el-button>
    </template>
  </template>
</el-upload>
</template>
<script>
export default {
  name: 'OUpload',
  props: ['item', 'value'],
  data () {
    return {
      fileList: this.value, // 
    }
  },
  methods: {
    // 
    handleInput (val) {
      this.$emit('input', val)
    },
    // 
    onBeforeUpload (file, item) {
      if (item.fileType && !item.fileType.includes(file.type)) {
        this.$message.error('')
        return false
      }
      // K
      if (item.fileSize && item.fileSize < file.size / 1024) {
        this.$message.error('')
        return false
      }
      const imgPromise = (type, item) => new Promise((resolve, reject) => {
        const reader = new FileReader()
        reader.readAsDataURL(file)
        reader.onload = (e) => {
          const image = new Image()
          image.src = e.target.result
          image.onload = () => {
            const w = image.width
            const h = image.height
            if (type === 1) {
              if (!(w === item.width && h === item.height)) {
                this.$message.error(item.tips || ` ${item.width}px*${item.height}px!`)
                reject(Boolean(false))
              } else {
                resolve(true)
              }
            } else if (type === 2) {
              if (w / h !== item.ratio) {
                this.$message.error(item.tips || '!')
                reject(Boolean(false))
              } else {
                resolve(true)
              }
            }
          }
        }
      })

      // 
      if (item.fileLimit) {
        const { ratio } = item.fileLimit
        if (ratio) {
          return imgPromise(2, item.fileLimit)
        }
        return imgPromise(1, item.fileLimit)
      }
    },
    // 
    // handleFileChange (file, fileList) {
    //   debugger
    //   this.fileList = fileList
    // },
    // 
    handleFileRemove (file, fileList) {
      this.$emit('input', fileList)
    },
    // 
    onUploadSuccess (res, file, fileList) {
      // 
      const { response = '', code = 'code', data = 'data', msg = 'msg', codeVal = 0 } = this.item.response || { }
      const result = response ? res[response] : res
      fileList.pop()
      // 
      const arr = fileList.slice()
      if (result[code] === codeVal || result[code] === 200) {
        const { name = '', url = '' } = result[data]
        arr.push({
          name,
          url
        })
      } else {
        // 
        this.fileList.pop()
        this.$message.error(result[msg])
      }
      this.fileList = arr
      this.$emit('input', arr)
    },
    // 
    onFileExceed () {
      this.$message.error('')
    }
  }
}
</script>
